.App {
  font-family: "Helvetica Neue";
  background-color: #FAA916;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

/* HEADER */

.header {
  font-size: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.header * {
  margin: 10px;
}
.header h6 {
  font-style: italic;
}

/* QUESTION CONTAINER */

.question-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.question-container .buzzer {
  background: -webkit-gradient(linear, left top, left bottom, from(#da4f49), to(#b73a34));
  background: -webkit-linear-gradient(#da4f49, #b73a34);
  background: -o-linear-gradient(#da4f49, #b73a34);
  background: linear-gradient(#da4f49, #b73a34);
  color: #E7DFE8;
  font-size: 16px;
  padding: 4px 12px;
  border-radius: 5px;
  border-color: #b73a34;
  outline: none;
}
.question-container .join-button {
  background: #6D676E;
  color: #FAA916;
  font-size: 16px;
  padding: 4px 12px;
  border-radius: 5px;
  border-color: #6D676E;
  outline: none;
  margin: 10px 0px 10px 0px;
}

.question-container .guess-field {
  background-color: #E7DFE8;
  font-size: 16px;
  padding: 5px 5px;
}
/* MESSAGES */

.chat-container {
  width: 700px;
  height: 250px;
  background-color: #E7DFE8;
  border: 2px solid;
  position: relative;
}
.chat-container input {
  width: 650px;
  position: absolute;
  bottom: 0;
}
.chat-container button {
  position: absolute;
  bottom: 0;
  right: 0;
}
#messages li {
  list-style-type: none;
}
.question-container .individual-question {
  padding: 30px;
  border: 5px solid;
  border-radius: 25px;
  width: 700px;
  -webkit-box-shadow: 10px 10px;
          box-shadow: 10px 10px;
  margin: 0px 0px 40px 0px;
  height: 180px;
}

.question-container .new-question-button {
  background: #6D676E;
  color: #FAA916;
  font-size: 16px;
  padding: 4px 12px;
  border-radius: 5px;
  border-color: #6D676E;
  outline: none;
  margin: 30px 0px;
}
.question-container .player-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding: 0px;
}

.question-container .player-list li{
  margin: 0px 50px;
  font-size: 20px;
  -webkit-box-shadow: 5px 5px;
          box-shadow: 5px 5px;
  padding: 20px;
  border: 2px solid;
  border-radius: 3px;
  list-style-type: none;
}




